@import "@/assets/global.scss";




































.banner {
  z-index: 99;
  width: 100%;
  height: 52px;
  position: fixed;
  top: 0;
  background: #FFFFFF;
  text-align: center;
  padding: 0 16px;

  > .link-img {
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translate(0, -50%);
  }

  > .skip {
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translate(0, -50%);
  }
}
