@import "@/assets/global.scss";



























































































































































































.slot-span {
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translate(0, -50%);
}

.main {
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  overflow-y: scroll;
  padding: 52px 0 52px 0;
  box-sizing: border-box;
}

.card-panel {
  width: 100%;
  height: 175px;
  padding: 20px 0;
  border-bottom: 1px solid #E9E9E8;
  display: flex;
  justify-content: center;

  > .card {
    width: 312px;
    height: 135px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: linear-gradient(259.4deg, #FCA311 0%, #FC8211 100%);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
    border-radius: 12px;
  }
}

.expired-panel {
  width: 100%;
  height: 54px;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #E9E9E8;

  > div {
    width: 320px;
    padding: 14px 0 14px 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.tab-panel {
  width: 100%;
  height: 39px;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #E9E9E8;

  > div {
    width: 320px;
    display: flex;
    justify-content: center;

    > div {
      @extend .font-color-6;
      width: 160px;
      padding: 9px 0;
    }

    > .selected {
      @extend .font-color-8;
      border-bottom: 3px solid #FCA311;
    }
  }
}

.tab-panel-fixed {
  z-index: 10;
  width: 100%;
  height: 39px;
  background: #FFFFFF;
  position: fixed;
  top: 52px;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #E9E9E8;

  > div {
    width: 320px;
    display: flex;
    justify-content: center;

    > div {
      @extend .font-color-6;
      width: 160px;
      padding: 9px 0;
    }

    > .selected {
      @extend .font-color-8;
      border-bottom: 3px solid #FCA311;
    }
  }
}

.content-panel {
  width: 100%;
  margin: 0 auto;
}
