@import "@/assets/global.scss";























.log-panel {
  width: 100%;
  border-bottom: 1px solid #E9E9E8;
  display: flex;
  justify-content: center;

  > div {
    width: 320px;
    padding: 20px 16px 16px 20px;
    position: relative;

    > div {
      text-align: left;
    }
  }
}

.log-panel-used {
  height: 96px;
}

.log-panel-new {
  height: 128px;
}

.points {
  position: absolute;
  top: 16px;
  right: 0;
  width: 90px;
  padding: 4px 18px;
  background: #FCA311;
  border-radius: 100px;
  box-sizing: border-box;
}
